.feature-cards-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 130px;


    .feature-cards-section_cont {
        width: calc(100% - 200px);
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 40px;

        .feature-cards-section_title {
            width: 70%;
            text-align: center;
            font-family: Poppins, sans-serif;
            font-size: 44px;
            font-style: normal;
            font-weight: 400;
            line-height: 124%;
            /* 54.56px */
            letter-spacing: -0.88px;
            background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }

    .feature-cards-grid{
        width: 100%;
    }
}